<template>
  <div class="page page-background index">
    <img src="../../assets/page-4-many.png" class="index-many">
    <img src="../../assets/page-4-black.png" class="index-black">
    <p class="index-title">{{ items[current].title }}</p>
    <div class="index-desc">
      <div class="index-desc-content">
        <template v-for="item in items[current].desc">
          <p class="index-desc-content-main" :key="item.main">{{ item.main }}</p>
          <p
            class="index-desc-content-sub"
            v-if="item.sub"
            :key="item.sub">{{ item.sub }}</p>
        </template>
      </div>
    </div>
    <ui-next v-show="showLogo" @click="gotoNextPage"  />
    <transition leave-active-class="animated fadeOut">
      <div v-if="current === 0" v-show="showMask" class="page-index-mask" />
    </transition>
    <transition
      enter-active-class="animated fadeIn">
      <img :src="items[current].img" v-show="showLogo" class="index-logo">
    </transition>
  </div>
</template>

<script>
import UiNext from '../../ui/next/next'
import items from './index-text'
import nextpage from '../../components/mixin/next-page'

export default {
  components: {
    UiNext
  },
  mixins: [nextpage],
  data () {
    return {
      showMask: true,
      showLogo: false,
      items: items,
      current: 0
    }
  },
  methods: {
    gotoNextPage () {
      if (this.current === 0) {
        this.gotoSpecialPage(1)
      } else if (this.current === 3) {
        this.gotoSpecialPage(2)
      } else {
        this.$router.push({
          name: 'page-4-detail',
          params: {
            index: this.current + 1
          }
        })
      }
    },
    gotoSpecialPage (index) {
      this.$router.push({
        name: `page-4-special-${index}`
      })
    }
  },
  created () {
    this.current = this.$route.params.index - 1
    this.showLogo = this.current !== 0
  },
  mounted () {
    setTimeout(() => {
      this.showLogo = true
    }, 1000)
    setTimeout(() => {
      this.showMask = false
    }, 2000)
  }
}
</script>

<style lang="less" scoped>
.index {
  &-many {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
  }
  &-logo {
    position: absolute;
    top: .29rem;
    left: .94rem;
    width: 1.9rem;
  }
  &-black {
    position: absolute;
    left: 0;
    bottom: 1.62rem;
    width: 100%;
  }
  &-title {
    position: absolute;
    color: #333;
    width: 100%;
    text-align: center;
    font-size: .14rem;
    line-height: .2rem;
    top: 2.05rem;
    left: 0;
  }
  &-desc {
    position: absolute;
    display: flex;
    left: 0;
    bottom: 1.62rem;
    width: 100%;
    height: 2.13rem;
    background: url('../../assets/page-4-black.png') center center no-repeat;
    background-size: cover;
    color: #fff;
    align-items: center;
    justify-content: space-around;
    &-content {
      &-main {
        font-size: .14rem;
        line-height: .23rem;
        padding-left: .32rem;
        background: url('../../assets/page-4-light.png') center left no-repeat;
        background-size: .23rem .23rem;
      }
      &-sub {
        padding-left: .32rem;
        font-weight: lighter;
      }
    }
  }
}
</style>




