export default {
  props: {
    changeRoute: String
  },
  watch: {
    changeRoute (v) {
      if (v === 'next') {
        this.$emit('finished-change', '')
        this.gotoNextPage()
      } else if (v === 'previous') {
        this.$emit('finished-change', '')
        this.gotoPreviousPage()
      }
    }
  },
  methods: {
    gotoPreviousPage () {
      // const index = this.getCurrentPage()
      // if (index !== '' && index > 1) {
      //   this.$router.push({ name: `page-${index - 1}-index` })
      // }
    },
    getCurrentPage () {
      const name = this.$router.currentRoute.name
      const index = name.split('-').length === 3 ? parseInt(name.split('-')[1], 10) : ''
      return index
    }
  }
}



