<template>
  <ui-container class="page page-background question">
    <p class="question-intro">你真的了解年轻消费者吗？</p>
    <p class="question-desc">三道题检验一下</p>
    <transition
      appear
      appear-active-class="animated flipInY"
      enter-active-class="animated flipInY"
      leave-active-class="animated flipOutY">
      <div v-show="showIndex" class="question-index">{{ current + 1 }}</div>
    </transition>
    <transition
      appear
      appear-active-class="animated bounceInDown"
      enter-active-class="animated bounceInDown"
      leave-active-class="animated bounceOutUp">
      <p v-show="showIndex" class="question-title">{{ questions[current].title }}</p>
    </transition>
    <div class="question-answers">
      <transition-group
        enter-active-class="animated fadeIn">
      <p
        class="question-answers-item"
        v-for="(text, index) in options"
        :key="text"
        @click="handleAnswerClick(index)">
        {{ text }}
      </p>
      </transition-group>
    </div>
    <transition
      enter-active-class="animated bounceInUp">
      <p class="question-result" v-show="showResult">{{ resultText }}</p>
    </transition>
    <div
      v-if="current < 2"
      v-show="showNext"
      @click="hanldeNextClick({ name: 'page-1-question', params: { index: current + 2 }})"
      class="question-next">
      下一题
    </div>
    <div
      v-else
      v-show="showNext"
      @click="hanldeNextClick({ name: 'page-2-index' })"
      class="question-nextpage">
      95后，了解一下？
    </div>
  </ui-container>
</template>

<script>
import UiContainer from '../../components/container'
import quesitons from './questions.js'

export default {
  data () {
    return {
      questions: quesitons,
      current: 0,
      showResult: false,
      showNext: false,
      showIndex: true,
      resultText: '',
      answers: [0, 0, 0],
      options: []
    }
  },
  components: {
    UiContainer
  },
  methods: {
    handleAnswerClick (index) {
      if (index === this.answers[this.current]) {
        this.resultText = '答对！你一定是95后！'
      } else {
        this.resultText = '答错了！暴露年龄啦！ 有点懵？'
      }
      this.showResult = true
      this.showNext = true
    },
    initOptions () {
      setTimeout(() => {
        this.options = this.questions[this.current].answers.slice(0, 1)
      }, 100)
      setTimeout(() => {
        this.options = this.questions[this.current].answers.slice(0, 2)
      }, 600)
      setTimeout(() => {
        this.options = this.questions[this.current].answers.slice(0, 3)
      }, 1100)
    },
    hanldeNextClick (payload) {
      this.$router.push(payload)
    }
  },
  created () {
    this.current = Math.min(this.$route.params.index, 3) - 1
  },
  mounted () {
    this.initOptions()
  },
  watch: {
    '$route' () {
      this.showIndex = false
      this.options = []
      setTimeout(() => {
        this.current = Math.min(this.$route.params.index, 3) - 1
        this.showIndex = true
        this.initOptions()
      }, 750)
      this.showResult = false
      this.showNext = false
    }
  }
}
</script>

<style lang="less" scoped>
.question {
  padding-top: .24rem;
  p {
    text-align: center;
  }
  &-intro {
    position: relative;
    color: #4A4A4A;
    font-size: .14rem;
    line-height: .2rem;
  }
  &-desc {
    position: relative;
    margin-top: .04rem;
    color: #1F1F1F;
    font-size: .2rem;
    line-height: .28rem;
  }
  &-title {
    position: relative;
    width: 100%;
    margin-top: .22rem;
    font-size: .18rem;
    line-height: .25rem;
    text-align: center;
    color: #1F1F1F;
  }
  &-answers {
    position: relative;
    width: 2.88rem;
    margin: .52rem auto 0;
    border-top: 1px solid #333;
    &-item {
      position: relative;
      text-align: center;
      width: 100%;
      padding: .15rem 0 .14rem;
      border-bottom: 1px solid #333;
      font-size: .24rem;
      line-height: .33rem;
      color: #1f1f1f;
      font-weight: lighter;
      &:active {
        color: #fff;
      }
    }
  }
  &-result {
    position: relative;
    margin-top: .32rem;
    font-weight: lighter;
  }
  &-index {
    position: relative;
    margin: .2rem auto 0;
    width: .35rem;
    height: .35rem;
    background: url('../../assets/page-1-oval.png') center center no-repeat;
    background-size: cover;
    color: #fff;
    font-size: .22rem;
    line-height: .3rem;
    padding-left: .12rem;
    padding-top: .03rem;
  }
  &-next {
    position: absolute;
    color: #fff;
    font-size: .2rem;
    line-height: .28rem;
    left: 1.58rem;
    bottom: .46rem;
    z-index: 10;
    &:active {
      color: #4a4a4a;
    }
  }
  &-nextpage {
    position: absolute;
    text-align: center;
    left: .81rem;
    bottom: .32rem;
    width: 2.13rem;
    padding: .14rem 0;
    background:linear-gradient(-168.1deg,rgba(255,88,186,1),rgba(243,52,135,1));
    box-shadow: 0px 2px 4px 0px rgba(246,246,246,0.5);
    border-radius: 8px;
    font-size: .2rem;
    line-height: .28rem;
    color: #fff;
  }
}
</style>



