<template>
  <ui-container
    :logo="logo"
    title="撩对人，就成功了一半"
    @next-click="handleNextClick">
    <div class="content">
      粉丝当道，爱豆就是消费风向标。QQ广告聚集庞大粉丝用户，配合腾讯多维标签，助力美妆广告的精准定向投放。 
    </div>
    <div class="image">
      <div class="image-label">
        <p>腾讯DMP多维标签</p>
        <p style="color: #FEA5D4;">智选TA</p>
      </div>
      <img class="image-1" src="../../assets/page-4-detail-1.png">
      <img class="image-2" src="../../assets/page-4-detail-1-2.png">
    </div>
  </ui-container>
</template>

<script>
import UiContainer from '../../components/page-4-container'
import someinfo from './index-text'

export default {
  data () {
    return {
      logo: someinfo[0].img
    }
  },
  components: {
    UiContainer
  },
  methods: {
    gotoNextPage () {
      this.$router.push({
        name: 'page-4-detail',
        params: {
          index: 1
        }
      })
    },
    handleNextClick () {
      this.gotoNextPage()
    }
  }
}
</script>

<style lang="less" scoped>
.content {
  position: relative;
  color: #fff;
  font-size: .14rem;
  line-height: .25rem;
  margin-top: .2rem;
}
.image {
  position: relative;
  margin-top: .28rem;
  &-1 {
    width: 1.9rem;
  }
  &-2 {
    position: absolute;
    width: 1.82rem;
    bottom: 0;
    right: 0;
  }
  &-label {
    position: absolute;
    top: .32rem;
    right: 0;
    text-align: center;
    color: #fff;
    font-size: .12rem;
    line-height: .17rem;
  }
}
</style>





