import Pages1Index from './pages/1/index'
import Pages1Detail from './pages/1/detail'
import Pages1Question from './pages/1/question'
import Pages2Index from './pages/2/index'
import Pages2Detail from './pages/2/detail'
import Pages3Index from './pages/3/index'
import Pages4Before from './pages/4/before'
import Pages4Index from './pages/4/index'
import Pages4Detail from './pages/4/detail-2'
import Pages4Detail1 from './pages/4/detail-1'
import Pages4Detail3 from './pages/4/detail-3'
import Pages5Index from './pages/5/index'
import test from './components/container'

export default {
  routes: [
    {
      path: '/',
      redirect: '/1'
    },
    {
      path: '/1',
      name: 'page-1-index',
      component: Pages1Index
    },
    {
      path: '/1/detail',
      name: 'page-1-detail',
      component: Pages1Detail
    },
    {
      path: '/1/question/:index',
      name: 'page-1-question',
      component: Pages1Question
    },
    {
      path: '/2',
      name: 'page-2-index',
      component: Pages2Index
    },
    {
      path: '/2/detail/:index',
      name: 'page-2-detail',
      component: Pages2Detail
    },
    {
      path: '/3',
      name: 'page-3-index',
      component: Pages3Index
    },
    {
      path: '/4/before',
      name: 'page-4-before',
      component: Pages4Before
    },
    {
      path: '/4/:index',
      name: 'page-4-index',
      component: Pages4Index
    },
    {
      path: '/4/spcial/1',
      name: 'page-4-special-1',
      component: Pages4Detail1
    },
    {
      path: '/4/spcial/2',
      name: 'page-4-special-2',
      component: Pages4Detail3
    },
    {
      path: '/4/detail/:index',
      name: 'page-4-detail',
      component: Pages4Detail
    },
    {
      path: '/5',
      name: 'page-5-index',
      component: Pages5Index
    },
    {
      path: '/test',
      name: 'test',
      component: test
    }
  ]
}



