<template>
  <ui-container
    :logo="items[current].logo"
    :title="items[current].title"
    @next-click="handleNextClick">
    <div class="content">
       {{ items[current].desc }}
    </div>
    <div class="image">
      <img
        class="image-item"
        v-for="img in items[current].imgs"
        :key="img"
        :src="img">
    </div>
  </ui-container>
</template>

<script>
import UiContainer from '../../components/page-4-container'
import nextpage from '../../components/mixin/next-page'
import items from './detail-text'

export default {
  data () {
    return {
      items: items,
      current: 0
    }
  },
  created () {
    this.current = Math.min(this.$route.params.index - 1, this.items.length - 1)
  },
  mixins: [nextpage],
  components: {
    UiContainer
  },
  methods: {
    gotoNextPage () {
      this.$router.push({
        name: 'page-4-index',
        params: {
          index: this.current + 2
        }
      })
    },
    handleNextClick () {
      this.gotoNextPage()
    }
  }
}
</script>

<style lang="less" scoped>
.content {
  position: relative;
  color: #fff;
  font-size: .14rem;
  line-height: .25rem;
  margin-top: .2rem;
}
.image {
  position: relative;
  margin-top: .28rem;
  &-item {
    position: relative;
    display: block;
    margin: .28rem auto 0;
    width: 1.9rem;
  }
}
</style>





