<template>
  <ui-container :show-spot="showManyGif" class="index">
    <ui-logo class="index-logo" />
    <img v-show="showManyGif" class="index-many" src="../../assets/page-1-many.gif">
    <img src="../../assets/page-1-oval.png" class="index-oval">
    <p class="index-desc">找到年轻消费者的正确打开方式，其实并不难。</p>
    <ui-next v-show="showManyGif" @click="gotoNextPage"  />
    <transition
      leave-active-class="animated fadeOut">
      <div v-show="!closeMaks" class="index-mask" />
    </transition>
    <transition
      enter-active-class="animated fadeIn">
      <img
          v-show="showLight"
          src="../../assets/page-1-title.png"
          class="index-title"
          :class="{'index-title-shrink' : showContent}">
    </transition>
  </ui-container>
</template>

<script>
import UiLogo from '../../ui/logo/logo'
import UiNext from '../../ui/next/next'
import UiContainer from '../../components/container'
import nextpage from '../../components/mixin/next-page'

export default {
  data () {
    return {
      showContent: false,
      showManyGif: false,
      closeMaks: false,
      showLight: false,
      list: {
        showLight: 500,
        showContent: 1500,
        closeMaks: 2500,
        showManyGif: 3000
      }
    }
  },
  mixins: [nextpage],
  components: {
    UiLogo,
    UiNext,
    UiContainer
  },
  methods: {
    gotoNextPage () {
      this.$router.push({
        name: 'page-1-question',
        params: {
          index: 1
        }
      })
    },
    showElements () {
      Object.keys(this.list).forEach(key => {
        setTimeout(() => {
          this[key] = true
        }, this.list[key])
      })
    }
  },
  mounted () {
    this.showElements()
  }
}
</script>

<style lang="less" scoped>
.index {
  &-title {
    position: absolute;
    display: block;
    top: 1.88rem;
    width: 2.86rem;
    left: 50%;
    margin-left: -1.43rem;
    transition: all 1s ease;
    &-shrink {
      top: 2.1rem;
      width: 1.66rem;
      margin-left: -.83rem;
    }
  }
  &-mask {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    width: 100%;
    height: 100%;
  }
  &-many {
    position: absolute;
    top: .65rem;
    left: .28rem;;
    width: 3.2rem;
  }
  &-oval {
    position: absolute;
    top: 1.26rem;
    left: .78rem;
    width: 2.2rem;
    height: 2.2rem;;
  }
  &-logo {
    position: absolute;
    top: .18rem;
    left: .13rem;
  }
  &-desc {
    position: absolute;
    top: 4.48rem;
    left: .82rem;
    width: 2.13rem;
    text-align: center;
    color: #1f1f1f;
    font-size: .14rem;
    line-height: .28rem;
  }
}
</style>




