<template>
  <div class="page page-background index">
    <img v-show="!showMask" src="../../assets/page-3-light-black.gif" class="index-lightblack">
    <transition enter-active-class="animated lightSpeedIn">
      <img v-show="showText" src="../../assets/page-3-text.png" class="index-text">
    </transition> 
    <transition-group
      enter-active-class="animated fadeIn">
      <div
        v-for="(item, index) in items"
        :key="item"
        :class="`index-item index-item-${index + 1}`">
        {{ item }}
      </div>
    </transition-group>
    <img src="../../assets/page-3-kouhong.png" class="index-kouhong">
    <ui-next v-show="showText" @click="gotoNextPage"  />
    <transition leave-active-class="animated fadeOut">
      <div v-show="showMask" class="page-index-mask" />
    </transition>
    <img v-show="showMask" src="../../assets/page-3-light.gif" class="index-lightblack">
  </div>
</template>

<script>
import UiNext from '../../ui/next/next'
import nextpage from '../../components/mixin/next-page'

export default {
  components: {
    UiNext
  },
  data () {
    return {
      showMask: true,
      showText: false,
      items: []
    }
  },
  mixins: [nextpage],
  methods: {
    gotoNextPage () {
      this.$router.push({
        name: 'page-4-before'
      })
    },
    initItems () {
      setTimeout(() => {
        this.items.push('年轻人在哪里？')
      }, 100)
      setTimeout(() => {
        this.items.push('凭什么认识你？')
      }, 800)
      setTimeout(() => {
        this.items.push('凭什么爱上你？')
      }, 1500)
    }
  },
  mounted () {
    setTimeout(() => {
      this.showMask = false
    }, 3000)
    setTimeout(() => {
      this.showText = true
      this.initItems()
    }, 3800)
  }
}
</script>

<style lang="less" scoped>
.index {
  &-lightblack {
    position: absolute;
    width: 1.95rem;
    top: .7rem;
    left: .9rem;
  }
  &-text {
    position: absolute;
    width: 3.28rem;
    top: 1.85rem;
    left: .25rem;
  }
  &-kouhong {
    position: absolute;
    width: .56rem;
    top: 4.36rem;
    left: .25rem;
  }
  &-item {
    position: absolute;
    color: #fff;
    text-align: center;
    &-1 {
      top: 2.89rem;
      left: .31rem;
      padding-top: .4rem;
      width: 3.13rem;
      height: .79rem;
      background: url('../../assets/page-3-item-1.png') center center no-repeat;
      background-size: cover;
    }
    &-2 {
      top: 3.8rem;
      left: .7rem;
      padding-top: .36rem;
      width: 2.63rem;
      height: .72rem;
      background: url('../../assets/page-3-item-2.png') center center no-repeat;
      background-size: cover;
    }
    &-3 {
      top: 4.78rem;
      left: .55rem;
      padding-top: .2rem;
      width: 3.04rem;
      height: .6rem;
      background: url('../../assets/page-3-item-3.png') center center no-repeat;
      background-size: cover;
    }
  }
}
</style>




