<template>
  <div class="page detail">
    <img :src="logo" class="detail-logo">
    <div class="detail-title">
      <p class="detail-title-desc">举例</p>
      <p class="detail-title-main">{{ title }}</p>
    </div>
    <slot />
    <ui-next @click="handleClick" />
  </div>
</template>

<script>
import UiNext from '../ui/next/next'

export default {
  props: {
    logo: String,
    title: String
  },
  components: {
    UiNext
  },
  methods: {
    handleClick (e) {
      this.$emit('next-click', e)
    }
  }
}
</script>

<style lang="less" scoped>
.detail {
  height: auto;
  min-height: 100%;
  background-color: #3A3839;
  padding: .38rem .31rem .8rem;
  &-logo {
    position: relative;
    display: block;
    width: 1.9rem;
    margin: 0 auto;
  }
  &-title {
    margin-top: -.26rem;
    &-desc {
      position: relative;
      color: #F33487;
      font-size: .14rem;
      line-height: .2rem;
    }
    &-main {
      position: relative;
      color: #fff;
      font-size: .2rem;
      line-height: .28rem;
    }
  }
}
</style>




