<template>
  <ui-container class="detail">
    <transition
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut">
      <img
        v-show="showContent"
        :src="details[current].img"
        class="detail-img"
        :class="`detail-img-${current + 1}`">
    </transition>
    <transition
      enter-active-class="animated flipInX"
      leave-active-class="animated flipOutX">
      <p v-show="showTitle" class="detail-title">{{ details[current].title }}</p>
    </transition>
    <transition
      enter-active-class="animated bounceInUp"
      leave-active-class="animated bounceOutDown">
      <p v-show="showContent" class="detail-content">{{ details[current].content }}</p>
    </transition>
    <ui-next @click="gotoNextPage"  />
  </ui-container>
</template>

<script>
import UiContainer from '../../components/container'
import UiNext from '../../ui/next/next'
import nextpage from '../../components/mixin/next-page'

export default {
  data () {
    return {
      current: 0,
      details: [
        {
          img: require('../../assets/page-2-detail-1.png'),
          title: '95后开始自嘲，00后风生水起',
          content: '当90后自称腊肉，QQ上最活跃的那批也已换成95和00后，他们独立特行、不避物欲，迅速长成新一代消费力量。'
        },
        {
          img: require('../../assets/page-2-detail-2.png'),
          title: '看脸的世界，颜值即正义',
          content: '变美、爱美、讨论美是年轻人共识，从外貌到QQ皮肤，有关颜值的探讨永远在线。'
        },
        {
          img: require('../../assets/page-2-detail-3.png'),
          title: '每天必做三项：种草、拔草和拍照',
          content: '耳机、键盘、美食、美妆，一刻拔草不停，时时都在晒单，对于自己关注的领域，年轻人的消费与分享乐此不疲。'
        },
        {
          img: require('../../assets/page-2-detail-4.png'),
          title: '既要好看的皮囊，也要有趣的灵魂',
          content: '以前，他们聊星座，现在，他们聊护肤和妆容。话题更新，达人频出，好奇心正破除着新一代年轻人社交的边界。'
        }
      ],
      showTitle: false,
      showContent: false
    }
  },
  mixins: [nextpage],
  components: {
    UiContainer,
    UiNext
  },
  methods: {
    gotoNextPage () {
      if (this.current === this.details.length - 1) {
        this.$router.push({
          name: 'page-3-index'
        })
      } else {
        this.toggleElements()
      }
    },
    toggleElements () {
      this.showTitle = false
      this.showContent = false
      setTimeout(() => {
        this.current = Math.min(this.details.length - 1, this.current + 1)
        this.showTitle = true
        this.showContent = true
      }, 1000)
    }
  },
  created () {
    this.current = Math.min(this.details.length - 1, this.$route.params.index - 1)
  },
  mounted () {
    setTimeout(() => {
      this.showTitle = true
      this.showContent = true
    }, 200)
  }
}
</script>

<style lang="less" scoped>
.img (@width, @top, @left) {
  width: @width;
  top: @top;
  left: @left;
}
.detail {
  padding: 3.87rem .31rem 0;
  &-img {
    position: absolute;
    &-1 {
      .img(2.07rem, 1.12rem, .84rem);
    }
    &-2 {
      .img(2.02rem, 1.04rem, .87rem);
    }
    &-3 {
      .img(1.77rem, 1rem, .99rem);
    }
    &-4 {
      .img(1.33rem, .75rem, 1.21rem);
    }
  }
  &-title {
    width: 100%;
    text-align: left;
    font-size: .18rem;
    line-height: .25rem;
    color: #1F1F1F;
  }
  &-content {
    margin-top: .22rem;
    width: 100%;
    font-size: .14rem;
    line-height: .25rem;
    color: #4a4a4a;
  }
}
</style>



