<template>
  <div class="page index">
    <div class="index-mask" />
    <div class="index-content page-background">
      <div class="index-mirror">
        <p class="index-mirror-content">
          聚会带妆，出门洗头；寂寞群聊，火爆拼邮。 现代青年们兜里有点积蓄、心里有点追求，并沉迷剁手，成为美妆消费的砥柱中流。
        </p>
      </div>
      <img src="../../assets/page-2-many.png" class="index-many">
      <ui-next @click="gotoNextPage" v-show="showNext" />
    </div>
  </div>
  
</template>

<script>
import UiNext from '../../ui/next/next'
import nextpage from '../../components/mixin/next-page'

export default {
  data () {
    return {
      showNext: false
    }
  },
  mixins: [nextpage],
  components: {
    UiNext
  },
  methods: {
    gotoNextPage () {
      this.$router.push({
        name: 'page-2-detail',
        params: {
          index: 1
        }
      })
    }
  },
  mounted () {
    setTimeout(() => {
      this.showNext = true
    }, 7000)
  }
}
</script>

<style lang="less" scoped>
.index {
  &-content {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
    animation: clip 6s ease;
  }
  &-many {
    position: absolute;
    top: 38px;
    left: 0;
    width: 246px;
  }
  &-mirror {
    position: absolute;
    left: 113px;
    top: 297px;
    width: 246px;
    height: 269px;;
    background: url('../../assets/page-2-mirror.png') center center no-repeat;
    background-size: cover;
    &-content {
      position: absolute;
      width: 149px;
      font-size: 14px;
      line-height: 20px;
      top: 49px;
      left: 60px;
      color: #333333;
    }
  }
  &-light {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    animation: gradient 6s ease;
    background: radial-gradient(60px at 1.14rem 1.14rem, transparent 0px, transparent 114px, #000);
    // background: -webkit-radial-gradient(1.14rem 1.14rem, circle closest-corner, transparent 0px, transparent 114px, #000);
    background-size: 200% 200%;
    background-position: 1.26rem 2.99rem;
  }
  &-mask {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #000;
  }
}

@keyframes clip {
  0% {
    clip-path: circle(114px at 0 0);
  }
  20% {
    clip-path: circle(114px at 261px 137px);
  }
  40% {
    clip-path: circle(114px at 0 274px);
  }
  65% {
    clip-path: circle(114px at 261px 481px);
  }
  80% {
    clip-path: circle(114px at 243px 412px);
  }
  100% {
    clip-path: circle(600px at 243px 412px);
  }
}
</style>



