<template>
  <div
    id="app"
    :style="style"
    @touchstart="handleTouchStart"
    @touchmove="handleTouchMove"
    @touchend="handleTouchEnd">
    <!-- <transition
      enter-active-class="animated slideInUp"
      leave-active-class="animated slideOutUp"
      mode="out-in"> -->
      <router-view
        :change-route="changeRoute"
        @finished-change="handleFinishedChange">
      </router-view>
    <!-- </transition> -->
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      touchStart: -1,
      touchEnd: -1,
      changeRoute: '',
      isMove: false,
      shouldPrevent: true
    }
  },
  computed: {
    style () {
      const width = window.innerWidth + 'px'
      const height = window.innerHeight + 'px'
      return {
        width,
        height
      }
    }
  },
  methods: {
    handleTouchMove (e) {
      const name = this.$route.name
      console.log(name)
      if (name !== 'page-4-special-1' && name !== 'page-4-special-2' && name !== 'page-4-detail') {
        e.preventDefault()
      } else {
        this.isMove = false
        return
      }
      this.isMove = true
      this.touchEnd = e.touches[0].clientY
    },
    handleTouchStart (e) {
      this.touchStart = e.touches[0].clientY
    },
    handleTouchEnd (e) {
      if (!this.isMove) {
        return
      }
      if (this.touchStart - this.touchEnd > 140) {
        this.changeRoute = 'next'
      } else if (this.touchStart - this.touchEnd < -140) {
        this.changeRoute = 'previous'
      } else {
        this.changeRoute = ''
      }
      this.isMove = false
    },
    handleFinishedChange (value) {
      this.changeRoute = value
    }
  },
  mounted () {
    // TODO resize
  }
}
</script>

<style lang="less" scoped>
#app {
  background-color: #000;
}
</style>



