<template>
  <ui-container
    :logo="logo"
    title="年轻人Always Online，营销也要Always On"
    @next-click="handleNextClick">
    <div class="items">
      <div
        class="items-item"
        v-for="(item, index) in items"
        :key="index">
        <img class="items-image" :class="`items-image-${index + 1}`" :src="item.img">
        <p class="items-title">{{ item.title }}</p>
        <p class="items-desc">{{ item.desc }}</p>
      </div>
      <p>QQ广告“Always on”营销解决方案三步完美订制品牌邂逅。</p>
    </div>
  </ui-container>
</template>

<script>
import UiContainer from '../../components/page-4-container'

export default {
  data () {
    return {
      logo: require('../../assets/page-4-ttj.png'),
      items: [
        {
          img: require('../../assets/page-4-detail-5-1.png'),
          title: 'STEP 1：数据',
          desc: '为兰蔻打造千万级专属人群DMP。'
        },
        {
          img: require('../../assets/page-4-detail-5-2.png'),
          title: 'STEP 2：场景',
          desc: '依托稳定ID，让触达看似不经意，其实很精心。'
        },
        {
          img: require('../../assets/page-4-detail-5-3.png'),
          title: 'STEP 3：创意',
          desc: '创意永远在线，比你想象得更懂你。'
        }
      ]
    }
  },
  methods: {
    handleNextClick () {
      this.$router.push({
        name: 'page-5-index'
      })
    }
  },
  components: {
    UiContainer
  }
}
</script>

<style lang="less" scoped>
.content {
  position: relative;
  color: #fff;
  font-size: .14rem;
  line-height: .25rem;
  margin-top: .2rem;
}
.items {
  position: relative;
  margin-top: .28rem;
  color: #fff;
  font-size: .14rem;
  line-height: .25rem;
  &-item {
    margin-bottom: .4rem;
  }
  &-image {
    display: block;
    margin: 0 auto;
    &-1 {
      width: 1.9rem;
    }
    &-2 {
      width: 2rem;
    }
    &-3 {
      width: .69rem;
    }
  }
  &-title {
    width: 100%;
    text-align: center;
    font-weight: bold;
  }
  &-desc {
    width: 100%;
    text-align: center;
  }
}
</style>





