<template>
  <span
    class="ui-spot ui-spot-animation"
    :class="`ui-spot-animation-${index % 2 + 1}`"
    :style="style" />
</template>

<script>
export default {
  props: {
    widthRange: {
      type: Array,
      default: () => {
        return [0.4, 1]
      }
    },
    gradientRange: {
      type: Array,
      default: () => {
        return [
          [0.6, 0.9],
          [0.3, 0.5]
        ]
      }
    },
    xRange: {
      type: Array,
      default: () => {
        return [0, 0.8]
      }
    },
    yRange: {
      type: Array,
      default: () => {
        return [0.4, 0.9]
      }
    },
    index: Number
  },
  data () {
    return {}
  },
  computed: {
    style () {
      const background = `radial-gradient(circle closest-side, rgba(255, 255, 255, ${this.gradientRange[0][0] + (this.gradientRange[0][1] - this.gradientRange[0][0]) * Math.random()}), rgba(255, 255, 255, ${this.gradientRange[1][0] + (this.gradientRange[1][1] - this.gradientRange[1][0]) * Math.random()}) 80%, rgba(255, 255, 255, 0) 100%)`
      const width = `${this.rangeRandom(this.widthRange)}rem`
      const height = width
      const top = `${this.rangeRandom(this.yRange) * 100}%`
      const left = `${this.rangeRandom(this.xRange) * 100}%`
      return {
        top,
        left,
        background,
        width,
        height
      }
    }
  },
  methods: {
    rangeRandom (range) {
      return range[0] + (range[1] - range[0]) * Math.random()
    }
  }
}
</script>

<style lang="less" scoped>
.ui-spot {
  position: absolute;
  display: inline-block;
  z-index: 0;
  &-animation {
    animation-duration: 10s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    &-1 {
      animation-name: spot1;
    }
    &-2 {
      animation-name: spot2;
    }
  }
  
}
@keyframes spot1 {
  0% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
  25% {
    opacity: 0.6;
    transform: translate3d(40px, 0, 0) scale(0.6);
  }
  50% {
    opacity: 0.8;
    transform: translate3d(40px, 20px, 0) scale(0.8);
  }
  75% {
    opacity: 1;
    transform: translate3d(20px, 0px, 0) scale(1.2);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
}

@keyframes spot2 {
  0% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
  25% {
    opacity: 0.6;
    transform: translate3d(0, 40px, 0) scale(1.2);
  }
  50% {
    opacity: 0.8;
    transform: translate3d(20px, 40px, 0) scale(0.8);
  }
  75% {
    opacity: 1;
    transform: translate3d(30px, 30px, 0) scale(1.2);
  }
  100% {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
}
</style>



