<template>
  <div class="page before">
    <transition
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut">
      <img class="before-spot" v-show="showSpot" src="../../assets/page-4-before-spot.png">
    </transition>
    <transition
      enter-active-class="animated fadeIn"
      leave-active-class="animated wobble">
      <img class="before-bao" v-show="showBao" src="../../assets/page-4-before.png">
    </transition>
    <transition
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut">
      <div v-show="showText" class="before-sub">
        <p>又要好看，又要好玩</p>
        <p>美妆品牌在QQ上抓住年轻人的心</p>
      </div>
    </transition>
    <transition
      leave-active-class="animated fadeOut">
      <div class="before-btn" @click="handleClick">点击开启QQ锦囊</div>
    </transition>
  </div>
</template>

<script>
export default {
  methods: {
    handleClick () {
      this.showText = false
      this.showSpot = false
      this.showBao = false
      setTimeout(() => {
        this.$router.push({
          name: 'page-4-index',
          params: {
            index: 1
          }
        })
      }, 1200)
    },
    showElements () {
      Object.keys(this.list).forEach(key => {
        setTimeout(() => {
          this[key] = true
        }, this.list[key])
      })
    }
  },
  data () {
    return {
      showText: false,
      showSpot: false,
      showBao: false,
      list: {
        showText: 1500,
        showSpot: 500,
        showBao: 1000
      }
    }
  },
  mounted () {
    this.showElements()
  }
}
</script>

<style lang="less" scoped>
.before {
  background-color: #191416;
  &-spot {
    position: absolute;
    top: 0;
    left: .77rem;
    width: 2.22rem;
  }
  &-bao {
    position: absolute;
    width: .92rem;
    top: 3.39rem;
    left: 1.42rem;
  }
  &-sub{
    position: absolute;
    top: 1.56rem;
    left: 0;
    width: 100%;
    text-align: center;
    color: #fff;
    font-size: .14rem;
    line-height: .25rem;
  }
  &-btn {
    position: absolute;
    bottom: .46rem;
    width: 100%;
    text-align: center;
    font-size: .2rem;
    line-height: .28rem;
    font-weight: bold;
    color: #fff;
  }
}
</style>



