import Vue from 'vue'
import VueRouter from 'vue-router'
import RouterConfig from './router'
import App from './App.vue'
import './flexable'
import './ui/style'

Vue.use(VueRouter)

const router = new VueRouter(RouterConfig)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



