<template>
  <div class="page page-background">
    <slot />
    <ui-spot
      v-show="showSpot"
      v-for="i in count"
      :key="i"
      :index="i" />
  </div>
</template>

<script>
import UiSpot from '../ui/spot/spot'

export default {
  props: {
    count: {
      type: Number,
      default: 4
    },
    showSpot: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      styles: []
    }
  },
  methods: {
    genRandomStyles () {
      // const widthRange = [10, 90]
    }
  },
  components: {
    UiSpot
  }
}
</script>

<style lang="less" scoped>
.page-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
}
</style>



