<template>
  <img
    @click="handldClick"
    src="../../assets/page-common-next.png"
    class="ui-next animated infinite rubberBand">
</template>

<script>
export default {
  methods: {
    handldClick (e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style>
.ui-next {
  position: absolute;
  left: 50%;
  bottom: .2rem;
  width: .38rem;
  margin-left: -.19rem;
  z-index: 10;
}
</style>



